import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { SharedModule } from 'app/shared/shared.module';
import { ServicoListComponent } from './containers/servico-list/servico-list.component';
import { UnidadeListComponent } from './containers/unidade-list/unidade-list.component';
import { TipoListComponent } from './containers/tipo-list/tipo-list.component';

const routes: Route[] = [
  { path: 'servico', component: ServicoListComponent },
  { path: 'unidade', component: UnidadeListComponent },
  { path: 'tipo', component: TipoListComponent },
];

@NgModule({
  declarations: [
    ServicoListComponent,
    UnidadeListComponent,
    TipoListComponent
  ],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
})
export class GrupoServicoModule { }
